.search {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 500px;
    margin: 1em auto;
  }
  
  input {
    width: 300px;
    font-size: 1.4em;
    text-align: center;
    margin: 1em 0;
    font-family: "Bangers", cursive;
    padding: 0.5em 0;
  }
  
  button {
    font-size: 1.2em;
    padding: 0.5em 1em;
    margin-bottom: 1em;
    cursor: pointer;
    font-family: "Bangers", cursive;
    background: rgb(255, 240, 33);
    transition: linear 0.2s;
    border: 1px solid black;
    box-shadow: 2px 2px black;
  
    &:hover {
      transform: translate(-2px, -2px);
      box-shadow: 4px 4px black;
    }
  
    &.reset {
      margin-left: 0.25em;
      background-color: white;
    }
  }